import React, { Component } from 'react';
import '../css/footer.css'

class Footer extends Component {
  render() {
    return (
      <div className='retry'>
        <div>
          <button type="button" onClick={this.props.jugarDenuevo}>Juguemos denuevo!!</button>
        </div>
      </div>
    );
  }
}

export default Footer;



