import React, { Component } from 'react';
import Card from './Card'
import '../css/table.css'

class Table extends Component {
  render() {
    return (
      <div className="table">
        {
          this.props.cards.map((card, index) => {
            const comparando = this.props.parejas.indexOf(card) > -1;
            return <Card key={index} img={card.img} seleccionada={() => this.props.seleccionada(card)} comparando={comparando} acerto={card.acerto}/>
          })
        }
      </div>
    );
  }
}

export default Table;



