import Flipcard from "react-flipcard";
import React, { Component } from 'react';
import '../css/card.css'

class Card extends Component {
  render() {
    const visible = this.props.acerto ? 'hidden' : 'visible';
    let styles = {
      visibility: visible
    };

    return (
      <div className="card" onClick = {this.props.seleccionada}>
        <Flipcard flipped={this.props.comparando || this.props.acerto} disabled={true}>
          <div className="portada"></div>
          <div className="back">
            <img className="img" style={styles} src={this.props.img} alt="card" />
          </div>
        </Flipcard>
      </div>
    );
  }
}
export default Card;



